body {
  color: $textColorBlack;
}

.container {
  padding: 0 100px;

  max-width: 1920px;
  margin: 0 auto;
}

.title {
  font-family: 'Bebas Neue Cyrillic', sans-serif;
  font-weight: 400;
}

.parallax{
  &.transition{
    transition: .5s all linear;
  }
}

.button {
  -webkit-appearance: none;
  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 40px;

  &-green {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color: #FFFFFF;

    background: $greenColor;
    border-radius: 30px;

    border: 1px solid $greenColor;

    transition: .3s color cubic-bezier(.215,.61,.355,1), .3s background-color cubic-bezier(.215,.61,.355,1);

    &:hover{
      color:$greenColor;
      background-color: #fff;
    }
  }

  &-white {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color: $greenColor;
    border: 1px solid $greenColor;
    border-radius: 30px;

    transition: .3s color cubic-bezier(.215,.61,.355,1), .3s background-color cubic-bezier(.215,.61,.355,1);
    &:hover{
      color:#fff;
      background-color: $greenColor;
    }
  }

  &-black {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;

    color: #FFFFFF;
    background: #3E3E3E;
    border-radius: 40px;
  }

  &-arrow {
    display: flex;
    align-items: center;
    width: max-content;

    &:hover &__icon::before{
      transform: scale(0);
    }
    &:hover &__icon svg path{
      fill: #3E3E3E;
    }
    &:hover &__icon-green svg path{
      fill: $greenColor;
    }
    &:hover &__text{
      color:#3E3E3E;
      background-color: transparent;
      &-green{
        color:$greenColor;
      }
    }
    &__text {
      height: 47px;
      width: 255px;

      border-radius: 30px;

      border: 1px solid #3E3E3E;

      display: flex;
      align-items: center;
      justify-content: center;

      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;

      color: #fff;

      background-color: #3E3E3E;

      transition: .3s color cubic-bezier(.215,.61,.355,1), .3s background-color cubic-bezier(.215,.61,.355,1);

      &-green{
        border-color: $greenColor;

        background-color: $greenColor;
      }
    }
    &__icon {
      width: 47px;
      height: 47px;

      border-radius: 50%;
      border: 1px solid #3E3E3E;
      flex-shrink: 0;

      margin: 0 0 0 10px;

      position: relative;

      display: flex;
      align-items: center;
      justify-content: center;
      svg{
        position: relative;
        z-index: 2;
        path{
          fill:#FFFFFF;

          transition: .3s fill cubic-bezier(.215,.61,.355,1);
        }
      }

      &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;

        width: 100%;
        height: 100%;

        background-color: #3E3E3E;

        border-radius: 50%;

        transform: scale(1);

        transition: .3s transform cubic-bezier(.215,.61,.355,1);
      }

      &-green {
        border-color: $greenColor;

        svg{
          path{
            fill:#fff;
          }
        }

        &::before{
          background-color: $greenColor;
        }
      }
    }
  }
}

.icon {
  width: 100%;
  height: 100%;
}

.advantages, .tariffs, .statistics {
  &-scrollbar {
    &.swiper-scrollbar {
      position: relative;
      background: transparent;
      height: 3px;

      border-bottom: 1px solid #C7C7C7;

      .swiper-scrollbar {
        &-drag {
          height: 3px;
          background: #fff;
        }
      }
    }
  }
}

@media screen and (min-width: 1100px) {
  html,
  body {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }

  body{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 1600px) {
  .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
  .button{
    height: 55px;
    &-arrow{
      &__text{
        height: 55px;
        border-radius: 40px;
      }
    }
    &-white{
      border-radius: 40px;
    }
    &-green{
      border-radius: 40px;
    }
  }
}